import { FormGroup, FormBuilder, Validators } from '@angular/forms';
 import { DataserviceService } from '../dataservice.service';
 import { HttpClient, HttpHeaders } from '@angular/common/http';
 import {Router,ActivatedRoute} from '@angular/router';
 import {Pipe, PipeTransform} from '@angular/core';
import { text } from '@angular/core/src/render3/instructions';

@Component({
  selector: 'app-listings',
  templateUrl: './listings.component.html',
  styleUrls: ['./listings.component.css']
})
export class ListingsComponent implements OnInit {
  loginform : FormGroup
  products: Productsfetch[];
  filters : filter

  textvalue = ''
  bookcondition=["New","Almost New","Slight Damage","Worn"]
  constructor(private _dataService: DataserviceService,
    private fb: FormBuilder,
    private http: HttpClient,
    private router: ActivatedRoute,
     private route: Router,) { }

     getItems(){
      this._dataService.getProducts().subscribe(Prod =>{this.products = Prod;
        console.log(this.products);  
             
       })
     }

  ngOnInit() {
    this.loginform = this.fb.group({

  });
  this.filters = {
      condition :""  
  }   
    this.getItems()                 
  }
  listingsMenu(id){
    console.log(id);
    this.route.navigate(['listingid',id]);
  }

  sortFilter(shape){
  this.filters = {
    condition : shape.value
  }
     
      console.log(this.filters)  

    this.http.post('/api/products/filter',this.filters).subscribe((data:any) => {
      this.products = data
      console.log(data);
    });

 }
 onSearchChange(book :string ){
  
this.http.post('/api/products/filterbybook',{bookname : book}).subscribe((data:any) => {
  this.products = data
  console.log(data);
});
}
onSearchAuthor(author : string){
  this.http.post('/api/products/filterbyAuthor',{authorname : author}).subscribe((data:any) => {
    this.products = data    
    console.log(data);
  });
}
sortbyprice(price : number){
  if(price == 1){
  this._dataService.getbypricelow().subscribe(Prod =>{this.products = Prod;
    console.log(this.products);  
  })
}
  else if (price == 2){
    this._dataService.getbypricehigh().subscribe(Prod =>{this.products = Prod;
      console.log(this.products);  
    })
}

}

  }
  