import { Component, OnInit } from '@angular/core';
import {ParamMap} from "@angular/router";
import {Router,ActivatedRoute} from '@angular/router';
import {Productsfetch} from '../listbooks';
 import { DataserviceService } from '../dataservice.service';
 import { HttpClient,HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-listingid',
  templateUrl: './listingid.component.html',
  styleUrls: ['./listingid.component.css']
})
export class ListingidComponent implements OnInit {
  selected: number;
  list_id_pro: Productsfetch[];

  constructor( private _dataService: DataserviceService,
    private router: ActivatedRoute,
    private http: HttpClient,
    private route: Router) 
    { }

    getItems(){
      this._dataService.getProducts().subscribe(Prod =>{this.list_id_pro = Prod;
        console.log(this.list_id_pro);   
        console.log(this.selected);
        //if(this.selected==)
        let myid=JSON.stringify(this.list_id_pro);
  
       })
     }
  
    ngOnInit() {
      {
        this.router.paramMap.subscribe( (params: ParamMap) =>{
          this.selected = +(params.get('id'));
          
         
          this.getItems()
         
        });
      }
    }

    sendMessage(){
      if(localStorage.getItem('userId'))
      {
      this.http.post('/api/wish',{User_id:localStorage.getItem('userId'),Product_id:this.selected}).subscribe((data:any) => {
       console.log(data)
   
      if(data['message']){
          alert("You have already added to wishlist");
        }
        else{
          alert("Successfully added to wishlist");
        }
    })
  }
  else{
    alert("please log in first")
  } 
 }
    onSend(data){

      console.log(data)
      
       this.http.post('/api/message',{userid:localStorage.getItem('userId'),productid:this.selected,sellerid:this.list_id_pro[0]['sellerId'],message:data}).subscribe((data:any) => {
       console.log(data)
       if(data){
         alert("message send Successfully")
       }
    
     })

  
  }

}
